<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                placeholder="监测对象、设备编号、设备名称过滤"
                @callFunction="callFunction"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="builds"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column prop="siteshortname" label="监测对象">
                </el-table-column>
                <el-table-column
                    prop="fogcode"
                    label="设备编号"
                ></el-table-column>
                <el-table-column prop="switchno" label="开关号" width="70">
                </el-table-column>
                <el-table-column prop="fogname" label="设备名称">
                </el-table-column>
                <el-table-column prop="fogtype" label="设备类型">
                    <nobr slot-scope="scope">
                        {{
                            scope.row.fogtype ? mfogtype[scope.row.fogtype] : ""
                        }}
                    </nobr>
                </el-table-column>
                <el-table-column prop="fogstatus" label="在线" width="60">
                    <template slot-scope="scope">
                        <el-tag
                            :type="
                                scope.row.fogstatus == '1'
                                    ? 'success'
                                    : 'danger'
                            "
                            disable-transitions
                            >{{
                                scope.row.fogstatus == 1 ? "是" : "否"
                            }}</el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column prop="bwaterauto" label="自动喷水" width="80">
                    <template slot-scope="scope">
                        <el-tag
                            :type="
                                scope.row.bwaterauto == '1'
                                    ? 'success'
                                    : 'danger'
                            "
                            disable-transitions
                            >{{
                                scope.row.bwaterauto == "1" ? "是" : "否"
                            }}</el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column
                    prop="checkintime"
                    label="最后上线时间"
                    width="180"
                >
                </el-table-column>
                <el-table-column
                    prop="checkouttime"
                    label="最后离线时间"
                    width="180"
                >
                </el-table-column>
                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-col :span="12">
                        <el-form-item label="监测对象分组" prop="GROUPID">
                            <el-select
                                v-model="editForm.GROUPID"
                                filterable
                                placeholder="请选择"
                                @change="groupChange"
                            >
                                <el-option
                                    v-for="item in groups"
                                    :key="item.GROUPID"
                                    :label="item.groupshortname"
                                    :value="item.GROUPID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="监测对象" prop="SITEID">
                            <el-select
                                v-model="editForm.SITEID"
                                filterable
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in selectSites"
                                    :key="item.SITEID"
                                    :label="item.siteshortname"
                                    :value="item.SITEID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备类型" prop="fogtype">
                            <el-select
                                v-model="editForm.fogtype"
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="(value, key) in mfogtype"
                                    :key="key"
                                    :label="value"
                                    :value="Number(key)"
                                ></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备名称" prop="fogname">
                            <el-input
                                v-model="editForm.fogname"
                                auto-complete="off"
                                maxlength="20"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备编号" prop="fogcode">
                            <el-input
                                v-model="editForm.fogcode"
                                auto-complete="off"
                                maxlength="20"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="自动喷水" prop="bwaterauto">
                            <el-switch
                                v-model="editForm.bwaterauto"
                                active-value="1"
                                inactive-value="0"
                            >
                            </el-switch>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="开关号" prop="switchno">
                            <el-input
                                v-model="editForm.switchno"
                                auto-complete="off"
                                maxlength="5"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="喷雾持续分钟" prop="delay">
                            <el-input
                                v-model="editForm.delay"
                                auto-complete="off"
                                placeholder="自动喷雾持续分钟数（不填时默认30分钟）"
                                maxlength="10"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="addForm"
                    label-width="120px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-col :span="12">
                        <el-form-item label="监测对象分组" prop="GROUPID">
                            <el-select
                                v-model="addForm.GROUPID"
                                filterable
                                placeholder="请选择"
                                @change="groupChange"
                            >
                                <el-option
                                    v-for="item in groups"
                                    :key="item.GROUPID"
                                    :label="item.groupshortname"
                                    :value="item.GROUPID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="监测对象" prop="SITEID">
                            <el-select
                                v-model="addForm.SITEID"
                                filterable
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in selectSites"
                                    :key="item.SITEID"
                                    :label="item.siteshortname"
                                    :value="item.SITEID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备类型" prop="fogtype">
                            <el-select
                                v-model="addForm.fogtype"
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="(value, key) in mfogtype"
                                    :key="key"
                                    :label="value"
                                    :value="Number(key)"
                                ></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备名称" prop="fogname">
                            <el-input
                                v-model="addForm.fogname"
                                auto-complete="off"
                                maxlength="20"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设备编号" prop="fogcode">
                            <el-input
                                v-model="addForm.fogcode"
                                auto-complete="off"
                                maxlength="20"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="自动喷水" prop="bwaterauto">
                            <el-switch
                                v-model="addForm.bwaterauto"
                                active-value="1"
                                inactive-value="0"
                            >
                            </el-switch>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="开关号" prop="switchno">
                            <el-input
                                v-model="addForm.switchno"
                                auto-complete="off"
                                maxlength="5"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="喷雾持续分钟" prop="delay">
                            <el-input
                                v-model="addForm.delay"
                                auto-complete="off"
                                placeholder="自动喷雾持续分钟数（不填时默认30分钟）"
                                maxlength="10"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="addFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getfogpagelist,
    getfogGroups,
    removefog,
    editfog,
    addfog,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
import { mfogtype } from "../../assets/dic/dic";
export default {
    components: { leftgroup, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            builds: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                fogtype: [
                    {
                        required: true,
                        message: "请选择设备类型",
                        trigger: "blur",
                    },
                ],
                fogname: [
                    {
                        required: true,
                        message: "请填写设备名称",
                        trigger: "blur",
                    },
                ],
                fogcode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
                switchno: [
                    {
                        required: true,
                        message: "请填写设备开关号",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            addFormVisible: false,
            addLoading: false,
            addFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                fogtype: [
                    {
                        required: true,
                        message: "请选择设备类型",
                        trigger: "blur",
                    },
                ],
                fogname: [
                    {
                        required: true,
                        message: "请填写设备名称",
                        trigger: "blur",
                    },
                ],
                fogcode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
                switchno: [
                    {
                        required: true,
                        message: "请填写设备开关号",
                        trigger: "blur",
                    },
                ],
            },
            addForm: {},
            canEdit: false,
            canDelete: false,
            groups: [],
            selectSites: [],
            selectGroupId: 0,
            mfogtype: mfogtype,
        };
    },
    methods: {
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getPageList();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                groupid: this.selectGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getfogpagelist(paras).then((res) => {
                if (res.success) {
                    this.builds = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },
        getGroupCounts() {
            getfogGroups().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        groupChange(val) {
            this.getGroupSites(val);
            this.$set(this.addForm, "SITEID", "");
            this.$set(this.editForm, "SITEID", "");
            this.selectSites = this.groupSites;
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.FOGID };
                    removefog(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getPageList();
                        this.getGroupCounts();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });
                return;
            }
            this.editFormVisible = true;
            this.groupChange(row.GROUPID);
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {};
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    editfog(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getPageList();
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.editLoading = false;
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);
                    addfog(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getPageList();
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
    },
    mounted() {
        this.getPageList();
        this.getGroupCounts();
        this.getGroups();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
</style>


